@media (prefers-color-scheme: dark) {
  body {
    filter: invert(1);
    background: #000;
  }
}
body {
  font-family: Inter, "SF Compact", "Segoe UI", Roboto, sans-serif;
}

.hidden {
  display: none;
}

container {
  max-width: calc(100vw - 20%);
  width: 480px;
  margin: auto;
  display: block;
  padding-top: 24px;
  line-height: 28px;
}

h2 {
  margin: 48px auto 0;
  padding: 0;
  line-height: 20px;
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
  padding: 0;
  margin: 24px auto;
}

li > a {
  text-decoration: none;
  display: flex;
  flex-direction: column;
}

.date {
  font-size: 12px;
  opacity: 0.7;
  font-feature-settings: "tnum";
}

.projects ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 16px;
}
.projects ul a {
  border: 1px solid blue;
  border-radius: 12px;
  padding: 1rem;
  width: calc(100% - 2rem);
  text-decoration: none;
}
.projects ul a:hover {
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-color: green;
}
.projects ul a p {
  margin: 0;
  padding: 12px 0;
  font-size: 0.8rem;
  line-height: 1.3rem;
}
.projects ul date {
  font-size: 0.8rem;
  color: #636;
  font-feature-settings: "tnum";
}